.loader {
  width: 215px;
  height: 215px;
  box-sizing: border-box;
  background: #fff;
  margin: auto;
  display: block;
  position: relative;
}

.loader-text {
  text-align: center;
}

.loader:after {
  content: "";
  width: calc(100% - 30px);
  height: calc(100% - 15px);
  box-sizing: border-box;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0), rgba(255, 255, 255, .5) 50%, rgba(0, 0, 0, 0) 100%), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 16px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 50px, rgba(0, 0, 0, 0) 0);
  background-position: -185px 0, 50% 0, 50% 115px, 50% 142px;
  background-repeat: no-repeat;
  background-size: 75px 175px, 100% 100px, 100% 16px, 100% 30px;
  animation: animloader 1s linear infinite;
  position: absolute;
  top: 15px;
  left: 15px;
}

@keyframes animloader {
  to {
    background-position: 185px 0, 50% 0, 50% 115px, 50% 142px;
  }
}

.cat-info {
  gap: 20px;
  margin-top: 30px;
  padding: 5px;
  display: flex;
}

.cat__img {
  min-width: 100px;
  max-width: 500px;
}

.cat__breed-name {
  font-size: 20px;
  font-weight: 600;
}

.cat__breed-temperament {
  font-weight: 600;
}

.error {
  color: red;
}

